  }, [_vm._v("文本规范")]), _vm._v(" "), _c('markdown', [_vm._v("\n### 释义\n\n文本包含系统中所有的文本展示\n\n### 范围\n\n包含但不限于：系统数据展示、用户引导提示、错误提示、信息显示辅助说明(如标题)、按钮文字等\n\n### 整体文案风格\n\n**简洁:** 内容简洁，字数尽量少，避免冗余\n\n**易懂:** 文字易理解，拒绝个性化或不统一\n\n**颜色:** 文字的颜色和其意义符合，需遵循`设计风格-颜色`\n\n---\n\n### 常用格式\n\n常用格式定义了日常的信息展示方式\n\n### 统一交互\n\n#### 日期\n\n**分隔符:** '-'\n\n**小时制:** 24小时制\n\n#### 状态类\n\n**\b可用状态:** 如已启用、已生效、已通过\n\n**不可用状态:** 如已禁用、未生效、已驳回\n\n**挂起状态:** 如测试中、待领取\n\n#### 并列信息(id/name同时展示)\n\n分隔符: ' | '\n\n**两个信息:** 对于两个信息并列的情况，采用id | name\n\n**多信息** 对于多个信息并列的情况，采用id | code | name\n\n---\n\n约定：\n\n1. 默认情况下，当有以下类型操作时，请使用下面列出的文本，避免出现类似的文本。\n\n2. 当不足以表现其作用时，可以根据业务添加一些文字，如 `添加XXXX`、`编辑XXXX`。\n\n### 操作类型\n\n#### 1. 添加\n\n新增一项数据\n\n**类似文本：**新建、新增\n\n#### 2. 编辑\n\n编辑一项数据；“编辑”即编辑该项数据，或“编辑XXXX”编辑该项数据的局部数据或其中的某个属性数据，如“编辑标签”\n\n**类似文本：**修改\n\n#### 3. 查看[XXXX]\n\n查看一项数据的详细信息\n\n**类似文本：**详细信息\n\n#### 4. 启用\n\n将一项数据的状态变为“可使用”的状态\n\n**类似文本：**开启、使用、激活、生效\n\n#### 5. 禁用\n\n将一项数据的状态变为“禁用中”\n\n**类似文本：**关闭\n\n#### 8. 删除\n\n删除一项数据\n\n**类似文本：**清除、清空\n\n#### 9. 保存\n\n保存一个表单，保存内容，一般都要进行数据提交\n\n**类似文本：**确定，提交\n\n#### 10. 取消\n\n取消编辑，关闭弹窗，经常和“保存”一起出现\n\n#### 11. 提交审核\n\n将数据提交至审核处\n\n#### 12. 补发\n\n一般是奖品的补发\n\n#### 13. 导入XXXX\n\n导入一个文件，如“导入CSV文件”\n\n**类似文本：**上传\n\n#### 14. 导出\n\n将信息导出为一个文件\n\n**类似文本：**下载\n\n#### 15. 搜索\n\n搜索符合条件的内容\n\n**类似文本：**查询\n\n#### 16. 预览\n\n预览数据时，一般为数据配置后对前台产生的影响\n预览图片时，即查看原图\n\n**类似文本：**查看\n\n#### 17. 切换[XXXX]\n\n两种状态间的实时转换\n\n**类似处理：**tab页(IOS/Android)\n\n#### 18. XXXX配置\n\n对数据进行另一维度的配置，如当前是补丁数据列表，加入“补丁投放配置”操作对单个补丁做投放的配置\n\n**类似文本：**XXXX、XXXX设置\n\n---\n\n### 报错信息\n\n#### 非空报错\n\n没有填写/选择\n\n##### 语法\n\n请选择\n请填写\n\n#### 长度报错\n\n字符长度超限/不足\n\n##### 语法\n\n只能为X~XX个字符\n不能小于XXX个字符\n不能大于XXX个字符\n\n#### 格式报错\n\n内容与要求的格式不符\n\n##### 语法\n\n只能为XXXX\n必须包含XXX和XXX\n\n#### 系统报错\n\n通常需要和数据库匹配\n\n##### 语法\n　　\nXXXX错误\nXXXX已被占用，请使用其它的\nXXXX错误，您共有n次机会，现在还有n-1次机会\n\n**特别说明:** 提示信息的最后不加标点符号\n\n---\n\n### select第一项\n\n推荐根据场景使用如下三项：不限 全部 请选择\n\n### 数据为空\n\n前端默认展示为空，而非'-'等。\n\n例如表格中有些信息后端返回是空的\n")])], 1)
